<script>
export default {
  data() {
    return {
      percent: {
        value: 0,
      },
    };
  },
  methods: {
    runToTarget() {
      console.log("Running...");
      let per = this.percent;

      let id = null;
      let id2 = null;
      let redLine = document.getElementById("red-line");
      let redCircle = document.getElementById("red-circle");
      let i = 0;
      setTimeout(function() {
        id = setInterval(function() {
          per.value++;
          if (per.value == 79) {
            clearInterval(id);
          }
        }, 50);
        id2 = setInterval(function() {
          redLine.style.width = i++ + "%";
          if (i == 60) {
            redCircle.style.opacity = 1;
            clearInterval(id2);
          }
        }, 66);
      }, 1400);
    },
  },
  mounted() {
    this.runToTarget();
  },
};
</script>

