<script>
export default {
  methods: {
    changeBackground() {
      // this function to change background of the header when user scroll
      let header = document.getElementById("header");
      let changeable = true;
      window.onscroll = function() {
        let position = window.scrollY;
        if (position === 0) {
          header.style.backgroundColor = "transparent";
          changeable = true;
        } else if (position !== 0 && changeable) {
          header.style.backgroundColor = "rgba(10, 18, 39, 0.99)";
          changeable = false;
        }
      };
    },
    reload() {
      location.reload();
    },
  },
  mounted() {
    this.changeBackground();
  },
};
</script>

<style lang="scss" scoped>
header {
  display: flex;
  align-items: center;
  background-color: transparent;
  transition: all 0.5s;
  position: sticky;
  z-index: 10;
  top: 0;
  .logo {
    img {
      display: block;
      cursor: pointer;
    }
  }
  .menu {
    text-align: right;
    .ele {
      position: relative;
      display: inline-block;
      text-transform: capitalize;
      color: #fff;
      letter-spacing: 1px;
      transition: all 0.3s;
      &:nth-child(1):after,
      &:nth-child(2):after {
        content: "\f0a7";
        font-family: "Font Awesome 5 Free";
        font-size: 12px;
      }
      &:not(:first-child) {
        margin-left: 2rem;
      }
      &:last-child {
        text-transform: uppercase;
        font-weight: 550;
        padding: 5px 15px;
        border: 2px solid #f42f54;
        border-radius: 25px;
      }
      &:last-child:hover {
        color: #fff;
        background-color: #f42f54;
      }
      &:hover {
        cursor: pointer;
        color: rgb(253, 77, 77);
      }
      &:hover > ul {
        opacity: 1;
        visibility: visible;
      }
      .dropdown {
        display: flex;
        flex-wrap: wrap;
        position: absolute;
        left: calc(50% - 250px);
        width: 500px;
        height: 300px;
        padding: 2rem;
        background-color: rgba(32, 32, 32, 0.7);
        border-radius: 1rem;
        transition: all 0.3s;
        opacity: 0;
        visibility: hidden;
        li {
          flex-basis: 30%;
          text-align: left;
          color: #fff;
          &:hover {
            color: rgb(253, 77, 77);
          }
        }
      }
    }
  }
  i {
